<template>
<div>
    <Row type="flex" justify="center">
        <Col span="4">
            <Dropdown trigger="click" @on-click="changeType"
                style="width: 100%; height: 3em;">
                <Button style="width: 100%; height: 3em;">
                    {{ selectedTypeName }}
                    <Icon type="ios-arrow-down"></Icon>
                </Button>
                <DropdownMenu slot="list">
                    <DropdownItem v-for="type in types" :key="type.type"
                        :name="type.id">
                        {{ type.typeName }}
                    </DropdownItem>
                </DropdownMenu>
            </Dropdown>
        </Col>
        <Col span="16">
            <Input search v-model="searchContent" size="large"
                @on-search="search" placeholder="Enter something..." />
        </Col>
    </Row>
</div>
</template>

<script>
export default {
    data() {
        return {
            types: [
                {
                    id: 0,
                    type: 'keywords',
                    typeName: ''
                },
                {
                    id: 1,
                    type: 'titab',
                    typeName: ''
                },
                {
                    id: 2,
                    type: 'author',
                    typeName: ''
                }
            ],
            selectedType: 'titab',
            selectedTypeName: '',
            searchContent: ''
        }
    },
    methods: {
        changeType: function(name) {
            this.selectedType = this.types[name].type
            this.selectedTypeName = this.types[name].typeName
        },
        search: function() {
            if (this.searchContent != '') {
                this.$router.push({path: `/search/${this.selectedType}/${this.searchContent}`})
            }
        }
    }
}
</script>


<style scoped>
.drop-down {
    height: 100%;
    line-height: 100%;
}
</style>
